import React, { useEffect, useRef, useState, useCallback, useMemo } from 'react'
import './menu.less'
import ReactTooltip from 'react-tooltip';
import Icon from '../Icon/Icon';
import { useHistory, Link } from 'react-router-dom';
import UnderlineText from "../UnderlineText/UnderlineText"
import { inject, observer } from 'mobx-react';
import classNames from 'classnames';
import Button from '../Button/Button';
function Menu({ lang, closeSideMenu }) {
  const moreMenuRef = useRef(null);
  const docsMenuRef = useRef(null);
  const tradeMenuRef = useRef(null);
  const history = useHistory();
  let menus = useMemo(() => {
    let items = [
      {
        id: "trade",
        text: lang["trade"],
        ref: tradeMenuRef,
        isTabTrade: true,
        subMenu: [
          {
            id: "futures",
            text: lang["futures"],
            link: "https://deri.io/#/pro/trade/futures",
            target: true,
          },
          {
            id: "options",
            text: lang["options"],
            link: "https://deri.io/#/pro/trade/option",
            target: true,
          },
          {
            id: "powers",
            text: lang["powers"],
            link: `https://deri.io/#/pro/trade/power`,
            target: true,
          },
          {
            id: "gamma",
            text: lang["gamma"],
            link: `https://deri.io/#/pro/trade/gamma`,
            target: true,
          },
        ]
      },
      { id: "pools", text: lang["pools"], link: "https://deri.io/#/pool" ,target: true,},
      {
        id: "docs",
        text: lang["docs"],
        ref: docsMenuRef,
        subMenu: [
          {
            id: "white-paper",
            text: lang["white-paper"],
            link: "https://docs.deri.io/whitepaper",
            target: true,
          },
          {
            id: "code-audit",
            text: lang["code-audit"],
            link: "https://docs.deri.io/code-audits",
            target: true,
          },
          {
            id: "team",
            text: lang["team"],
            link: `https://misc.deri.io/#/team`,
            target: true,
          },
          {
            id: "faq",
            text: lang["faq"],
            link: "https://docs.deri.io/library/faqs",
            target: true,
          },
          {
            id: "guide",
            text: lang["guide"],
            link: "https://docs.deri.io/",
            target: true,
          },
          {
            id: "github",
            text: lang["github"],
            link: "https://github.com/deri-protocol",
            target: true,
          },
        ],
      },
      {
        id: "more",
        text: lang["more"],
        ref: moreMenuRef,
        isAddNewPage: true,
        subMenu: [
          {
            id: "deri-tools",
            text: lang["deri-tools"],
            link: `https://deri.io/#/deri-tools`,
          },
          {
            id: "bridge",
            text: lang["bridge"],
            link: `https://misc.deri.io/#/bridge`,
            target: true,
          },
          {
            id: "Referrals",
            text: "Referrals",
            link: `https://deri.io/#/referrals`,
          },
          {
            id: "token",
            text: lang["token"],
            link: `https://misc.deri.io/#/token`,
            target: true,
          },
          {
            id: "stats",
            text: lang["stats"],
            link: "https://info.deri.io/#/info",
            target: true,
          },
          // {
          //   id: "airdrop",
          //   text: lang["airdrop"],
          //   link: `https://misc.deri.io/#/airdrop`,
          //   target: true,
          // },
          {
            id: "governance",
            text: lang["governance"],
            link: `https://deri.io/#/governance`,
            target: true,
          },
          {
            id: "testnet",
            text: lang["testnet"],
            link: "https://testnet.deri.io",
            newPage: true,
          },
          // {
          //   id: "promotional-event",
          //   text: lang["promotional-event"],
          //   link: `https://misc.deri.io/#/trade-to-earn`,
          //   target: true,
          // },
          // {
          //   id: "retired-pool",
          //   text: lang["retired-pool"],
          //   link: `https://misc.deri.io/#/retired`,
          //   target: true,
          // },
        ],
      },
      {
        id: "v3",
        text: "V3",
        link: "https://v3.deri.io/#/index",
        target: true,
        legacy: true,
      },
      // {
      //   id: "eoc",
      //   text: lang["eoc"],
      //   link: `https://deri.fi/#/betit`,
      //   target: true,
      //   tip: "deri.fi",
      //   tipHtml: true,
      //   element: (
      //     <div className="deri-fi-box">
      //       <div className="deri-fi-text">
      //         <div className="deri-fi-logo-title">
      //           <Icon token="deri-fi-logo" />
      //           DERI.FI
      //         </div>
      //         <div className="deri-fi-connect">
      //           PORTAL FOR ALL DERI PROJECTS
      //         </div>
      //       </div>
      //       <div className="bg-color-box"></div>
      //     </div>
      //   ),
      // },
    ];
    return items;
  }, [lang]);

  const toIndex = useCallback(() => {
    closeSideMenu();
    history.push("/index");
  }, [closeSideMenu]);
  return (
    <div className="menu">
      <div className="back-icon">
        <div className="logo" onClick={toIndex}>
          <Icon token="logo" />
          <span className="name">DERI</span>
        </div>
        <Icon token="close-modal-icon" onClick={() => closeSideMenu()} />
      </div>
      {menus.map((menu) => {
        return (
          <span
            className={`menu-item ${menu.id}-item`}
            key={menu.id}
            onClick={() => !menu.subMenu && closeSideMenu()}
          >
            {menu.subMenu ? (
              <>
                <SubMenu menu={menu} closeSideMenu={closeSideMenu} />
                <MSubMenu menu={menu} closeSideMenu={closeSideMenu} lang={lang} />
              </>
            ) : menu.link.indexOf("https") === -1 ? (
              <Link to={menu.link}>
                {menu.tip ? (
                  <UnderlineText
                    tip={menu.tip}
                    html={menu.tipHtml}
                    element={menu.element}
                  >
                    {menu.text}
                  </UnderlineText>
                ) : (
                  menu.text
                )}
              </Link>
            ) : (
              <a
                rel="noreferrer"
                href={menu.link}
                target={menu.target ? "_blank" : ""}
              >
                {menu.tip ? (
                  <UnderlineText
                    tip={menu.tip}
                    html={menu.tipHtml}
                    element={menu.element}
                  >
                    {menu.text}
                  </UnderlineText>
                ) : (
                  menu.text
                )}
                {menu.legacy ? (
                  <>
                    <span className="legacy">Legacy</span>
                  </>
                ) : null}
              </a>
            )}
          </span>
        );
      })}
      <Button
        className="btn feed-back"
        onClick={() => window.open("https://forms.gle/TvwJ2kiscYqKfLxJ8")}
        label={"FEEDBACK"}
        width={132}
        height={32}
        borderRadius={4}
        borderSize={0}
        fontSize={16}
        bgColor="#1A2D4A"
      />
      {/* <div className="anniversary-header-menu">
        <a href="https://deri.io/#/anniversary">
          <div>
            <span>2ND ANNIVERSARY &#129395; 🎉</span>
            <span className="color-text">$150,000</span>
          </div>
        </a>
      </div> */}
    </div>
  );
}

function SubMenu({ menu, closeSideMenu }) {
  const [isTooltipOpen, setTooltipOpen] = useState(false)

  const hideTooltipHandler = useCallback(() => {
    setTooltipOpen(false)
  }, [])

  const onTooltipTriggerHover = () => {
    setTooltipOpen(true);
  };

  const onTooltipTriggerLeave = () => {
    hideTooltipHandler();
  }


  useEffect(() => {
    return () => {
      document.removeEventListener("mouseleave", hideTooltipHandler)
    }
  }, [])
  return (
    <div
      className="sub-item-box"
      onMouseLeave={onTooltipTriggerLeave}
    >
      <div className='sub-item'
        onMouseEnter={onTooltipTriggerHover}
        onMouseLeave={onTooltipTriggerLeave}
      >
        {isTooltipOpen ? <div className="open-sub-item"></div> : null}
        {menu.isAddNewPage ? <div className='have-new-page'></div> : null}
        {menu.text}
        <Icon token={isTooltipOpen ? 'arrow-up' : 'arrow-down'} width='20' height='20' />
      </div>
      {isTooltipOpen ? (
        <div className="subMenu-item-box" onMouseEnter={onTooltipTriggerHover}>
          {menu.subMenu.map((subMenu) =>
            subMenu.id === "gamma" ? <div
              className={`sub-menu-item ${subMenu.id}-item`}
              key={subMenu.id}
            >
              {subMenu.text} (coming soon)
            </div> : <a
              rel="noreferrer"
              href={subMenu.link}
              target={subMenu.target ? "_blank" : ""}
              onClick={closeSideMenu}
              key={subMenu.id}
            >
              <div className={`sub-menu-item ${subMenu.id}-item`}>
                {subMenu.text}
                {subMenu.newPage ? <div className="new-page">New</div> : null}
              </div>
            </a>
          )}
          {menu.icon}
        </div>
      ) : null}
    </div>
  )
}

function MSubMenu({ menu, closeSideMenu, lang }) {
  const [isTooltipOpen, setTooltipOpen] = useState(false);
  const tooltipWrapperRef = useRef(null);
  const [tabTrade, setTabTrade] = useState("pro");
  const hideTooltipHandler = useCallback(() => {
    const tooltipWrapperEl = tooltipWrapperRef.current;
    if (tooltipWrapperEl) {
      ReactTooltip.hide(tooltipWrapperEl);
      setTooltipOpen(false);
      document.removeEventListener("click", hideTooltipHandler);
    }
  }, []);

  const onTooltipTriggerClick = useCallback(() => {
    const tooltipWrapperEl = tooltipWrapperRef.current;
    if (tooltipWrapperEl) {
      if (isTooltipOpen) {
        ReactTooltip.hide(tooltipWrapperEl);
        setTooltipOpen(false);
      } else {
        ReactTooltip.show(tooltipWrapperEl);
        setTooltipOpen(true);
        setTimeout(() => {
          document.addEventListener("click", hideTooltipHandler);
        }, 0);
      }
    }
  }, [hideTooltipHandler, isTooltipOpen]);

  const tradeLink = useMemo(() => {
    if (tabTrade === "pro") {
      return [
        {
          id: "futures",
          text: lang["futures"],
          link: "https://deri.io/#/pro/trade/futures",
        },
        {
          id: "options",
          text: lang["options"],
          link: "https://deri.io/#/pro/trade/option",
        },
        {
          id: "powers",
          text: lang["powers"],
          link: `https://deri.io/#/pro/trade/power`,
        },
        {
          id: "gamma",
          text: lang["gamma"],
          link: `https://deri.io/#/pro/trade/gamma`,
        },
      ];
    } else if (tabTrade === "lite") {
      return [
        {
          id: "futures",
          text: lang["futures"],
          link: "https://deri.io/#/lite/trade/futures",
        },
        {
          id: "options",
          text: lang["options"],
          link: "https://deri.io/#/lite/trade/option",
        },
        {
          id: "powers",
          text: lang["powers"],
          link: `https://deri.io/#/lite/trade/power`,
        },
      ];
    }
  }, [lang, tabTrade]);

  const swithcTab = useCallback(
    (e) => {
      e.stopPropagation();
      if (tabTrade === "pro") {
        setTabTrade("lite");
      } else {
        setTabTrade("pro");
      }
    },
    [closeSideMenu, tabTrade]
  );


  return (
    <div
      className={classNames("m-sub-menu-box", {
        "open-m-sub-menu": isTooltipOpen,
      })}
      onClick={onTooltipTriggerClick}
      ref={tooltipWrapperRef}
    >
      <div className="menu-box">
        {isTooltipOpen ? (
          <Icon token="arrow-left" className="arrow-left" />
        ) : null}
        <div className="menu-box-show">
          {menu.text}
          {!isTooltipOpen ? <Icon token="arrow-right" /> : null}
        </div>
        {isTooltipOpen ? <div className="menu-box-hr"></div> : null}
      </div>
      <div
        className={
          isTooltipOpen
            ? "m-sub-menu-item m-show-sub-menu-item"
            : "m-sub-menu-item m-hide-sub-menu-item"
        }
      >
        {menu.isTabTrade ? (
          <div
            className={classNames("m-tab-pro-lite", {
              "pro-box-type": tabTrade === "pro",
              "lite-box-type": tabTrade === "lite"
            })}
            onClick={(e) => {
              swithcTab(e);
            }}
          >
            <div
              className={classNames("pro-lite-check", {
                "pro-type": tabTrade === "pro",
                "lite-type": tabTrade === "lite",
              })}
            >
              {tabTrade === "pro" ? "Pro" : "Lite"}
            </div>
          </div>
        ) : null}
        {menu.isTabTrade ? tradeLink &&
          tradeLink.map((subMenu) => (
            <div
              className={`sub-menu-item ${subMenu.id}-item`}
              key={subMenu.id}
            >
              {" "}
              {<>
                {subMenu.id === "gamma" ? (
                  <span key={subMenu.id}>{subMenu.text} (coming soon)</span>
                ) : (
                  <a
                    rel="noreferrer"
                    href={subMenu.link}
                    onClick={closeSideMenu}
                  >
                    {subMenu.text}
                  </a>
                )}
              </>}
            </div>
          )) : menu.subMenu.map((subMenu) => (
            <div
              className={`sub-menu-item ${subMenu.id}-item`}
              key={subMenu.id}
            >
              {" "}
              <a href={subMenu.link} onClick={closeSideMenu} rel="noreferrer">
                {subMenu.text}
              </a>
              {subMenu.newPage ? <div className="new-page">New</div> : null}
            </div>
          ))}
      </div>
    </div>
  );
}
export default inject('wallet')(observer(Menu))